.userPage {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    margin-bottom: 20px;
    /*display: grid;*/
    /*grid-template-columns: 1fr 1fr;*/
    /*grid-template-rows: minmax(250px, auto);*/
    /*grid-gap: 2rem;*/
    /*justify-items: end;*/
    /*align-items: start;*/
    /*padding: 20px 147px 20px 0;*/
    /*border: solid darkgrey 1px;*/
}
.userBlock {
    display: grid;
    grid-template-rows: minmax(25px, auto);
    grid-template-columns: 1fr;
    grid-gap: 0.3rem;
    justify-items: center;
    align-items: center;
    border: solid darkgrey 1px;
    border-radius: 8px;
    min-height: 250px;
    background-color: whitesmoke;
    padding: 10px 0;
    margin: 15px;
}
.userPhoto {
    grid-row: 1;
    grid-column: 1;
    height: 175px;
    justify-self: center;
    align-self: start;
}
.photo {
    width: 150px;
    border-radius: 10px;
}
.name {
    grid-row: 2;
    grid-column: 1;
    padding-top: 10px;
    text-align: center;
    width: 200px;
}
