.follow {
    grid-row: 4;
    grid-column: 1;
    justify-self: center;
    align-self: center;
}
.follow button{
    cursor: pointer;
}

.selectedPage {
    font-weight: bold;
    font-size: 20pt;
    cursor: pointer;
}
span {
    cursor: pointer;
}
button {
    text-decoration: none;
    font-size: 0.9rem;
    font-weight: bold;
}
