}

@media (max-width: 767px) {
    .userPage {
        display: grid;
        grid-template-columns: 1fr;
        grid-template-rows: minmax(120px, auto);
        grid-gap: 1rem;
        justify-items: center;
        align-items: start;
        padding: 20px 80px 20px 0;
        border: solid darkgrey 1px;
    }
    .userBlock {
        display: grid;
        grid-template-rows: minmax(25px, auto);
        grid-template-columns: 25vw 1fr;
        grid-gap: 0.3rem;
        justify-items: center;
        align-items: center;
        border: solid darkgrey 1px;
        border-radius: 8px;
        min-height: 100px;
        background-color: whitesmoke;
        width: 90vw;
    }
    .userPhoto {
        grid-row: 1/3;
        grid-column: 1;
        justify-self: center;
        align-self: start;
        padding-top: 5px;
        padding-bottom: 0;
    }
    .photo {
        width: 20vw;
        border-radius: 8px;
    }
    .name {
        grid-row: 1;
        grid-column: 2;
        justify-self: start;
        align-self: start;
        padding-top: 5px;
    }
    .status {
        grid-row: 2/4;
        grid-column: 2;
        justify-self: start;
        align-self: start;
        padding-left: 5px;
        width: 60vw;
    }
    .follow {
        grid-row: 3;
        grid-column: 1;
        justify-self: center;
        align-self:start;
        padding-bottom: 10px;
    }
}